<template>
  <div id="app" class="container">
     <div class="page-header">
       <h1>e book ref with power of vue 2 & Firebase  </h1>
     </div>
     <div class="panel panel-default">
       <div class="panel-heading">
         <h3>Add Books</h3>
        </div>
        <div class="panel-body">
         <form id ="form" class="from-inline" v-on:submit.prevent="addBook">
            <div class="form-group">
              <label for="bookTitle">Title:</label>
              <input type="text"  id="bookTitle" class="form-control" v-model="newBook.title" >
            </div>
            <div class="form-group">
              <label for="bookAuthor">Author:</label>
              <input type="text"  id="bookAuthor" class="form-control" v-model="newBook.author" >
            </div>

            <div class="form-group">
              <label for="bookurl">Url:</label>
              <input type="text"  id="bookurl" class="form-control" v-model="newBook.url" >
            </div>

          <input type="submit" class="btn btn-primary" value="Add Book">

        </form>


        </div>

     </div>
     <div class="panel panel-default">
       <div class="panel-heading">
         <h3> Books Lists</h3>
       </div>
        <div class="panel-body">
          <table class ="table table-striped">
            <thead>
              <tr>
                <th>  
                  Title 
                </th>
                <th>  
                  Author
                </th>
                <th>
                  delete
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="book in books" :key="book.key"> 
                <td>
                 <a v-bind:href="book.url">{{book.title}}</a>
                </td>
                <td>
                  {{book.author}}
                </td>
                <td>
                  <span class="glyphicon glyphicon-trash" v-on:click="removeBook(book)"></span>
            
                </td>

              </tr>

            </tbody>

          </table> 
        </div>
     </div>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'

import Firebase from 'firebase'

import toastr from 'toastr'

let config = {

    apiKey: "AIzaSyAYzeTTpQmoopFdRACjwE1rIkWorvRe-ZU",
    authDomain: "e-book-ref.firebaseapp.com",
    databaseURL: "https://e-book-ref.firebaseio.com",
    projectId: "e-book-ref",
    storageBucket: "e-book-ref.appspot.com",
    messagingSenderId: "423864923789"
    }
let app = Firebase.initializeApp(config);    

let db = app.database();

let bookRef = db.ref('Books');
export default {
  name: 'App',
  firebase:  {
     books: bookRef
  },
  data() {
    return {
      newBook :{
        title: '',
        author:'',
        url: ''
      }
    }
  },
  methods: {
    addBook: function(){
      bookRef.push(this.newBook);
      this.newBook.title ='';
      this.newBook.author ='';
      this.newBook.url ='';

    },
    removeBook:function(book){
      bookRef.child(book['.key']).remove();
      toastr.success("Book removed");
    }


  }
  
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



